let rootInstance = null;
//   const instance = { dom, element, childInstances };
const DidactDOM = {
  // æ¸²æŸ“å…¥å£
  render(element, container) {
    // elementæ˜¯JSXå¯¹è±¡
    const prevInstance = rootInstance;
    const nextInstance = reconcile(container, prevInstance, element);

    rootInstance = nextInstance;
  },

  // æ›´æ–°ç»„ä»¶
  updateComponent(component) {
    const internalInstance = component.__internalInstance;

    reconcile(internalInstance.dom.parentNode, internalInstance, internalInstance.element);
  },
  setComponentProps(component, props) {
    component.props = props;
    return this.updateComponent(component);
  },
};

/**
 *
 * @param {*} parentDom
 * @param {*} instance å½“å‰çš„instanceå¯¹è±¡
 * @param {*} element
 * @return ç”Ÿæˆæ–°çš„instanceï¼ˆvdomï¼‰
 */
// 1. æ²¡æœ‰vdom, åˆ›å»º
// 2. å·²ç»æœ‰vdomï¼Œ æ›´æ–°è§¦å‘
// --- å¯¹æ¯”-è™šæ‹Ÿdomæ ‘ï¼Œ è¿›è¡Œå¢ã€åˆ ã€æ”¹ï¼ˆç±»å‹ç›¸åŒï¼‰ã€æ›¿æ¢ï¼ˆç±»å‹ä¸åŒï¼‰
function reconcile(parentDom, instance, element) {
  if (!instance) {
    // æ–°å¢
    const newInstance = instantiate(element);
    parentDom.appendChild(newInstance.dom);
    return newInstance;
  } else if (!element) {
    // åˆ é™¤
    parentDom.removeChild(instance.dom);
    return null;
  } else if (instance.element.type === element.type) {
    // ç›¸åŒç±»å‹

    // domåŸå§‹ç±»å‹
    if (typeof element.type === 'string') {
      // æ›´æ–°å±æ€§
      updateDomProperties(instance.dom, instance.element.props, element.props);
      // æ›´æ–°å­å…ƒç´ 
      instance.childInstances = reconcileChildren(instance, element);
      instance.element = element;
      return instance;
    } else {
      // ç»„ä»¶ç±»å‹
      // æ›´æ–°ç»„ä»¶props
      const component = instance.component;
      component.props = element.props;
      // é‡æ–°renderç»„ä»¶
      const childElement = component.render();
      const oldChildInstance = instance.childInstance;
      const childInstance = reconcile(parentDom, oldChildInstance, childElement); // å¯¹æ¯”-å‰©ä¸‹-å­©å­
      instance.dom = childInstance.dom;
      instance.childInstance = childInstance;
      instance.element = element;
      return instance;
    }
  } else {
    // ç±»å‹ä¸åŒï¼Œç›´æ¥æ›¿æ¢
    const newInstance = instantiate(element);
    parentDom.replaceChild(newInstance.dom, instance.dom);
    return newInstance;
  }
}

function reconcileChildren(instance, element) {
  const parentDom = instance.dom;
  const childInstances = instance.childInstances;
  const childElements = element.props.children || [];
  const count = Math.max(childInstances.length, childElements.length);
  const newChildInstances = [];

  for (let i = 0; i < count; i++) {
    // æœ‰å¯èƒ½æ–°å¢æˆ–å‡å°‘å­å…ƒç´ 
    const newChildInstance = reconcile(parentDom, childInstances[i], childElements[i]);
    newChildInstances.push(newChildInstance);
  }
  return newChildInstances.filter(instance => instance != null); // <---- 2
}
// åˆ›å»ºinstanceå¯¹è±¡
// æ–°å»ºè™šæ‹Ÿdomç»„ä»¶  instance:{dom, element, childInstance, component}
// æ–°å»ºè™šæ‹Ÿdomå…ƒç´  instance: {dom, element, childInstances}
function instantiate(element) {
  const { type, props } = element;
  // ---ç»„ä»¶ç±»å‹
  if (typeof type === 'function') {
    const instance = {};

    const component = createComponent(element, instance); // ç»„ä»¶å®ä¾‹
    const childElement = component.render(); // è°ƒç”¨ç»„ä»¶renderï¼Œè·å¾— ç»„ä»¶ element
    const childInstance = instantiate(childElement); // ç»„ä»¶å†…éƒ¨åªæœ‰ä¸€ä¸ªå­å…ƒç´ 
    const dom = childInstance.dom;
    Object.assign(instance, {
      dom, // ç»„ä»¶dom
      element, // jsxå¯¹è±¡
      childInstance, // ç»„ä»¶instance
      component, // ç»„ä»¶å®ä¾‹
    });
    return instance;
  }
  // ---domå…ƒç´ 
  const dom = type === TEXT_ELEMENT ? document.createTextNode('') : document.createElement(type);

  updateDomProperties(dom, [], props);
  const childInstances = (props.children || []).map(instantiate);
  const childDoms = childInstances.map(childInstance => childInstance.dom);
  childDoms.forEach(child => dom.appendChild(child));
  return {
    dom,
    element,
    childInstances,
  };
}

function updateDomProperties(dom, prevProps, nextProps) {
  // åˆ é™¤å·²ç»åˆ é™¤çš„å±æ€§
  for (let key in prevProps) {
    if (!key in nextProps) {
      removeAttribute({ key, value: prevProps[key], dom });
    }
  }
  // å¦‚æœå±æ€§æ”¹å˜ï¼Œ è®¾ç½®æ–°çš„å±æ€§
  for (let key in nextProps) {
    if (nextProps[key] !== prevProps[key]) {
      setAttribute({ key, value: nextProps[key], dom });
    }
  }
}

const isEvent = name => name.startsWith('on');

function removeAttribute({ key, value, dom }) {
  if (isEvent(key)) {
    const event = key.slice(2).toLocaleLowerCase();
    dom.removeEventListener(event, value);
  } else {
    setAttribute({ key, undefined, dom });
  }
}

function setAttribute({ key, value, dom }) {
  if (key === 'children') return;
  if (isEvent(key)) {
    // äº‹ä»¶
    const event = key.slice(2).toLocaleLowerCase();
    dom.addEventListener(event, value);
  } else if (key === 'style') {
    // æ ·å¼
    for (let name in value) {
      dom.style[name] = value[name];
    }
  } else if (key === 'className') {
    dom.setAttribute('class', value);
  } else {
    dom[key] = value;
    // textNodeæ²¡æœ‰setAttributeæ–¹æ³•
    dom.setAttribute && dom.setAttribute(key, value);
  }
}

// åˆ›å»ºç»„ä»¶å®ä¾‹
function createComponent(element, internalInstance) {
  const { type, props } = element;
  let component;
  if (type.prototype.render) {
    // ç±»ç»„ä»¶
    component = new type(props);
  } else {
    component = new Didact.Component(props);
    component.constructor = type;
    component.render = () => type(component.props);
  }
  component.__internalInstance = internalInstance;
  return component;
}
